import React from 'react'
import Avatar from '@material-ui/core/Avatar';
import '../assets/Chat/ChatPanel.css'
import {Link, useHistory} from 'react-router-dom'
import {connect} from 'react-redux'
import * as chat_actions from '../store/actions/chat_action'
//you need to wrap name, message, image, timestamp by {} otherwise, it would cause the error that is use array instead...
function ChatPanel(props) {
    console.log('props::',props.friend_data.Chat_ID)
    const link = `chat/${props.friend_data.name}/${props.friend_data.Chat_ID}/`
    function handleClick() {
        props.set_friend_image(props.friend_data.image)
    }
    return (
        <Link to={link} onClick={handleClick}>
        <div className='chatpanel'>
            <Avatar className='chatpanel_image' alt={props.friend_data.name}
            src={props.friend_data.image}/>
            <div className='chatpanel_detail'>
                <h2>{props.friend_data.name}</h2>
                <p>{props.friend_data.message}</p>
            </div>
            <p className='chatpanel_timestamp'>{props.friend_data.timestamp}</p>
        </div>
        </Link>
    )
}




const mapDispatchToProps = dispatch => {
    return {
        set_friend_image: (picture) => dispatch(chat_actions.setImage(picture))
    }
}

export default connect(null, mapDispatchToProps)(ChatPanel)
